import Vue from 'vue'
import Router from 'vue-router'


import Welcome from '@/pages/Welcome'
import Index from '@/pages/Index'

Vue.use(Router);


export default new Router({
  routes: [
    {
      path: '/',
      name: 'Welcome',
      component: Welcome
    },{
      path: '/index',
      name: 'Index',
      component: Index
    }
  ]
})



